  #answers {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 50px;
    /* background-color:#4493b3; */
    max-height: 100%;
    

  
    
  }
  #answers ul {
    list-style-type: none;
    
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  
  
  #answers img {
  width: 200px;
  height: 200px;
  background-color: white;
  object-fit: cover;
  margin: 10px;
  max-width: 100%;
  height: auto;
  object-fit: contain;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
  border: 1px solid #ccc;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
  }
  
  #answers img:hover {
    transform: scale(1.1);
  }
  